import * as THREE from 'three';
import { RendererThree, MercatorProjection, getCSVFromURL } from '../../../../lib';
import { getJSONFromURL, } from 'njscore';

import { FilterGLViewDarkForDarken } from '../01_JSAPI/Tint/FilterGLViewDarkForDarken';
import { UI } from '../01_JSAPI/UI';
export class Solution extends RendererThree {

    streetOSM: any = [];
    routesTreated: any = [];
    routesControl: any = [];
    zones30: any = [];
    zones15: any = [];
    routesMain: any = [];

    ui: UI;
    meshTwitter: THREE.Points;
    meshStreet: THREE.LineSegments;
    meshNode: THREE.Points;
    meshTreated: THREE.LineSegments;
    meshControl: THREE.LineSegments;
    meshPolygon: THREE.Mesh;
    meshRoutesMain: THREE.LineSegments;

    meshAreaGon: THREE.Mesh;

    meshPolygon2: THREE.Mesh;

    pts: number[] = [];

    constructor(id: string = 'main') {
        super(id)

        const light = new THREE.PointLight(0xffffff, 0.8);
        this.camera.add(light);
        this.scene.add(light);

        const canvasGL = document.createElement('canvas');
        canvasGL.style.position = 'absolute';
        canvasGL.style.pointerEvents = 'none';
        canvasGL.style.mixBlendMode = 'darken';
        canvasGL.width = this.hostDiv.clientWidth;
        canvasGL.height = this.hostDiv.clientHeight;
        this.hostDiv.appendChild(canvasGL);
        const glCtx = canvasGL.getContext('webgl');
        const d = new FilterGLViewDarkForDarken(glCtx, 0.5, 5)
        d.Render(glCtx);

        this.ui = new UI(this);

        this.camera.position.set(0, 40, 48);

        const path = '/static/research/slow-zone/';
        getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/street_osm.geojson').then((data) => {
            for (let i = 0; i < data['features'].length; ++i) {
                this.streetOSM.push(data['features'][i]['geometry']['coordinates']);
            }
            this.meshStreet = renderLines(this.scene, this.streetOSM, [0, 1, 1]);
            this.meshNode = renderPoints(this.scene, this.streetOSM, [0, 1, 1]);


            getCSVFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/twitter_data_2010-2015.json').then((d) => {

                const xOFf = -15478803.88731685;
                const yOFf = -4766681.448690384;
                const scale = 0.01;

                const line = d.split(/\r\n|\r|\n/g);
                // console.log(line)
                const colors = [];
                for (let i = 1; i < line.length; ++i) {
                    const values = line[i].split(',');
                    const lon = +values[3];
                    const lat = +values[4];
                    if ((lon !== 0 && lat !== 0) && (!isNaN(lon) && !isNaN(lat))) {

                        const [xPro, yPro] = MercatorProjection(lon, lat);

                        const x = (xPro + xOFf) * scale
                        const y = (yPro + yOFf) * scale

                        this.pts.push(x, 0, y);
                        colors.push(0, 0.5, 0.5);
                    }
                }

                // console.log(this.pts)
                const geometry = new THREE.BufferGeometry();
                const material = new THREE.PointsMaterial({ vertexColors: true, size: 1.5 });
                geometry.setAttribute('position', new THREE.Float32BufferAttribute(this.pts, 3));
                geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
                geometry.computeBoundingSphere();

                this.meshTwitter = new THREE.Points(geometry, material);
                this.scene.add(this.meshTwitter);


            });


            getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/Paris-main-st.geojson').then((data) => {
                for (let i = 0; i < data['features'].length; ++i) {
                    this.routesMain.push(data['features'][i]['geometry']['coordinates']);
                }
                this.meshRoutesMain = renderLines(this.scene, this.routesMain, [1, 1, 0]);
            });

            getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/treated-st.geojson').then((data) => {
                for (let i = 0; i < data['features'].length; ++i) {
                    this.routesTreated.push(data['features'][i]['geometry']['coordinates']);
                }
                this.meshTreated = renderLines(this.scene, this.routesTreated, [1, 0, 0]);
            });

            getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/control-st.geojson').then((data) => {
                for (let i = 0; i < data['features'].length; ++i) {
                    this.routesControl.push(data['features'][i]['geometry']['coordinates']);
                }
                this.meshControl = renderLines(this.scene, this.routesControl, [0, 1, 0]);
            });

            getJSONFromURL('https://raw.githubusercontent.com/NamjuLee/data/master/Paris/zones-30.geojson').then((data) => {
                for (let i = 0; i < data['features'].length; ++i) {
                    this.zones30.push(data['features'][i]['geometry']['coordinates']);
                }
                // this.meshPolygon = renderMultiPolygon(this.scene, this.zones30, [0, 1, 0]);

            });

            this.start();
        });
    }

    public update(scene: THREE.Scene) {

        if (this.meshTwitter !== undefined) {
            if (this.ui.isActive_high_low_connectedness_user) { this.meshTwitter.visible = true; } else { this.meshTwitter.visible = false; }
        }

        if (this.meshRoutesMain !== undefined) {
            if (this.ui.isActive_Paris_main_st) { this.meshRoutesMain.visible = true; } else { this.meshRoutesMain.visible = false; }
        }

        if (this.meshStreet !== undefined) {
            if (this.ui.isActive_streetOSM) { this.meshStreet.visible = true; } else { this.meshStreet.visible = false; }
        }

        if (this.meshNode !== undefined) {
            if (this.ui.isActive_twitter) { this.meshNode.visible = true; } else { this.meshNode.visible = false; }
        }

        if (this.meshTreated !== undefined) {
            if (this.ui.isActive_treated_st) { this.meshTreated.visible = true; } else { this.meshTreated.visible = false; }
        }

        if (this.meshControl !== undefined) {
            if (this.ui.isActive_control_st) { this.meshControl.visible = true; } else { this.meshControl.visible = false; }
        }

        if (this.meshPolygon !== undefined) {
            if (this.ui.isActive_zones_30) { this.meshPolygon.visible = true; } else { this.meshPolygon.visible = false; }
        }


        // this.meshPolygon2.visible = true;
        // const geo = this.meshAre
        // console.log(geo);

    }
}
const renderCustomPolygon = (scene: THREE.Scene, geojson: any) => {
    const xOFf = -15478803.88731685;
    const yOFf = -4766681.448690384;
    const scale = 0.01;
    const shapes = [];

    let count = 0;

    for (let index = 0; index < geojson['features'].length; ++index) {
        const multiPolygon = geojson['features'][index]['geometry']['coordinates'];
        const meta = geojson['features'][index];
        // console.log(meta)
        const meta_yr = meta['properties'].yr;
        // console.log(meta_yr)



        const shape = new THREE.Shape();
        const holes = [];
        const shapePts = [];
        for (let polyI = 0; polyI < multiPolygon.length; ++polyI) {
            const polygon = multiPolygon[polyI];

            // if (polyI === 0) {

            //     path.moveTo
            // }
            // console.log(multiPolygon)
            // break;
            //     console.log(polygon.length)


            let path = new THREE.Path();

            for (let i = 0; i < polygon.length; ++i) {
                const [xPro, yPro] = MercatorProjection(polygon[i][0], polygon[i][1]);

                const x = (xPro + xOFf) * scale
                const y = (yPro + yOFf) * scale
                //     // console.log(x, y)

                if (polyI === 0) {
                    shapePts.push(new THREE.Vector2(x, y));
                } else {
                    if (i === 0) {
                        path.moveTo(x, y);

                    } else {
                        path.lineTo(x, y);

                        if (i === polygon.length - 1) {
                            path.closePath()
                        }
                    }
                }


            }

            holes.push(path);
            // shape.uuid = meta_yr;

            // // shape
            // count++;
            // shape.holes.push()
            // break;
        }
        const shapeThree = new THREE.Shape(holes);
        // shapeThree.holes.push(...holes)
        shapes.push(shapeThree);
        // break;

    }


    let geometry = new THREE.ShapeGeometry(shapes);
    //     console.log(count)
    //     console.log(geometry)
    let mat = new THREE.MeshBasicMaterial({
        side: THREE.DoubleSide, color: 0x00ff00,
        transparent: true,
        opacity: 0.3
    });
    let mesh = new THREE.Mesh(geometry, mat);
    scene.add(mesh);
    mesh.rotateOnAxis(new THREE.Vector3(1, 0, 0), Math.PI * 0.5);
    return mesh;
}
const renderMultiPolygon = (scene: THREE.Scene, paths: any, colorArray: number[]) => {
    // if (multiPolygons === undefined) return;

    const xOFf = -15478803.88731685;
    const yOFf = -4766681.448690384;
    const scale = 0.01;

    // const geometry = new THREE.BufferGeometry();
    // const material = new THREE.MeshBasicMaterial( { vertexColors: true } );

    const positions = [];

    const shapes = [];



    for (let multi = 0; multi < paths.length; ++multi) {
        const multiPolygon = paths[multi];

        for (let polyI = 0; polyI < multiPolygon.length; ++polyI) {
            const polygon = multiPolygon[polyI];



            if (polygon.length === 1) {
                const thePolygon = polygon[0];

                const shape = [];

                for (let i = 0; i < thePolygon.length; ++i) {

                    const [xPro, yPro] = MercatorProjection(thePolygon[i][0], thePolygon[i][1]);

                    const x = (xPro + xOFf) * scale
                    const y = (yPro + yOFf) * scale
                    // console.log(x, y)
                    // positions.push(x, 0, y)

                    shape.push(new THREE.Vector2(x, y));
                }
                shapes.push(new THREE.Shape(shape));
            }
        }
    }


    let geometry = new THREE.ShapeGeometry(shapes);
    let mat = new THREE.MeshBasicMaterial({
        side: THREE.DoubleSide, color: 0x00ff00,
        transparent: true,
        opacity: 0.3
    });
    let mesh = new THREE.Mesh(geometry, mat);
    scene.add(mesh);
    mesh.rotateOnAxis(new THREE.Vector3(1, 0, 0), Math.PI * 0.5);
    return mesh;
};


const renderPoints = (scene: THREE.Scene, paths: any, color: number[]) => {
    const geometry = new THREE.BufferGeometry();
    const material = new THREE.PointsMaterial({ vertexColors: true, size: 0.5 });

    const positions = [];
    const colors = [];


    const xOFf = -15478803.88731685;
    const yOFf = -4766681.448690384;
    const scale = 0.01;


    for (let i = 0; i < paths.length; ++i) {
        const route = paths[i]

        for (let j = 0; j < route.length; ++j) {
            for (let k = 0; k < route[j].length; ++k) {

                const [xPro, yPro] = MercatorProjection(route[j][k][0], route[j][k][1]);

                const x = (xPro + xOFf) * scale
                const y = (yPro + yOFf) * scale

                positions.push(x, 0, y)

                colors.push(color[0], color[1], color[2]);


            }
        }
    }

    geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
    geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
    geometry.computeBoundingSphere();

    const mesh = new THREE.Points(geometry, material);
    scene.add(mesh);
    return mesh;

}

const renderLines = (scene: THREE.Scene, paths: any, color: number[]) => {

    const geometry = new THREE.BufferGeometry();
    // const material = new THREE.PointsMaterial( { vertexColors: true, size: 0.1 } );
    const material = new THREE.LineBasicMaterial({ vertexColors: true });

    const positions = [];
    const colors = [];
    const indices = [];

    const xOFf = -15478803.88731685;
    const yOFf = -4766681.448690384;
    const scale = 0.01;


    let xC = 0
    let yC = 0
    let Count = 0

    for (let i = 0; i < paths.length; ++i) {
        const route = paths[i]

        for (let j = 0; j < route.length; ++j) {
            for (let k = 0; k < route[j].length; ++k) {

                const [xPro, yPro] = MercatorProjection(route[j][k][0], route[j][k][1]);
                xC += xPro;
                yC += yPro;
                Count += 1;

                const x = (xPro + xOFf) * scale
                const y = (yPro + yOFf) * scale
                // console.log(x, y)
                positions.push(x, 0, y)

                colors.push(color[0], color[1], color[2]);

                if (k < route[j].length - 1) {
                    indices.push(Math.floor(positions.length / 3) - 1, Math.floor(positions.length / 3));
                }
            }
        }
    }
    // console.log(xC / Count, yC / Count)
    geometry.setIndex(indices);
    geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
    geometry.setAttribute('color', new THREE.Float32BufferAttribute(colors, 3));
    // generateMorphTargets( geometry );

    geometry.computeBoundingSphere();

    const mesh = new THREE.LineSegments(geometry, material);
    scene.add(mesh);
    return mesh;

};